main {
  background-color: #f4f4f4;
  padding: 20px;
}

body {
  color: #333;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.6;
}

section {
  background-color: #fff;
  border-radius: 8px;
  margin: 20px;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

.section-title {
  color: #3498db;
}

p {
  margin-bottom: 15px;
}

a {
  color: #3498db;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

h2 {
  font-size: 1.5em;
}

.container {
  flex-direction: row-reverse;
  gap: 10px;
  margin: 0 auto;
  display: flex;
  overflow: hidden;
}

aside nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

aside nav ul li {
  border: solid none;
  background-color: #d3d3d3;
  border-radius: 4px;
  margin-bottom: 10px;
  padding: 5px;
}

aside nav ul li a {
  color: #000;
  text-decoration: none;
  display: block;
}

aside nav ul li:hover {
  background-color: #3498db;
}

aside nav ul li:hover a {
  color: #fff;
  text-decoration: none;
}

aside {
  width: calc(30% - 10px);
}

.sections_wrapper {
  width: calc(70% - 10px);
}

@media screen and (max-width: 768px) {
  aside {
    display: none;
  }

  .sections_wrapper {
    width: 100%;
  }
}

table {
  border-collapse: collapse;
  border: 2px solid #000;
  width: 100%;
  margin-top: 20px;
}

th, td {
  text-align: left;
  border: 2px solid #000;
  padding: 8px;
}

th {
  background-color: #f2f2f2;
}

